<template>
	<div class="container">
		<mt-header class="header" title="">
			<header-item slot="left" v-bind:isBack=true v-on:onclick="goBack">
			</header-item>
			<div class="right" slot="right" @click="goRecord">
				<img src="../../assets/image/change-icon/btn_rule@2x.png"></div>
		</mt-header>		
		<div class="header-info">			
			<div class="title">
				{{scoreInfo.score}}
			</div>
			<div class="desc">
				{{scoreInfo.upcoming_score}}
				<img @click="touchExplain()" src="../../assets/image/change-icon/btn_integral@2x.png">
			</div>
		</div>		
		<div class="tab">
			<ul>
				<li class="tab-item" v-bind:class="{'active': currentIndex == 0}" @click="setCurrentItem(0)"></li>
				<li class="tab-item" v-bind:class="{'active': currentIndex == 1}" @click="setCurrentItem(1)"></li>
			</ul>
		</div>
		<div v-if="currentIndex == 0" class="list" v-infinite-scroll="getMore" infinite-scroll-disabled="canNotLoadMoer" infinite-scroll-distance="10">
			<score-item
				class="item"
				v-for="item in exchangeList"
				:key="item.id"
				:item="item"
				v-on:touchExchange="touchExchange(item)">
			</score-item>
		</div>
		<div v-if="currentIndex == 1" class="list" v-infinite-scroll="getMore" infinite-scroll-disabled="canNotLoadMoer" infinite-scroll-distance="10">
			<score-record
				class="item"
				v-for="item in recordList"
				:key="item.id"
				:item="item">
			</score-record>
		</div>		
		<div v-if="(!exchangeList.length && currentIndex == 0) || (!recordList.length && currentIndex == 1) " class="empty">
			<img src="../../assets/image/change-icon/e2_wu@2x.png">
			<div class="desc"></div>
		</div>
	</div>
</template>

<script>

import HeaderItem  from '../../components/common'
import ScoreItem from './child/ScoreItem'
import ScoreRecord from './child/ScoreRecord'
import * as api from '../../api/network/score'
import { MessageBox, Toast } from 'mint-ui'

export default {
	data() {
		return {
			scoreInfo: {},
			recordList: [],  // 
			exchangeList: [],  // 
			requestParams: {'page': 0, 'per_page': 10},  //
			loading: false,  //
			isMore: true,  //
			currentIndex: this.$route.query.index,  //tab			
		}
	},
	created() {
		this.getScoreInfo();
	},
	computed: {
	  canNotLoadMoer: function () {
	    return this.loading || !this.isMore;
	  },
	},
	methods: {
		touchExchange(item) {
			api.scoreExchangeCoupon(item.id).then(res => {
				if ( res ) {	
					Toast('')	
					this.getScoreInfo();			
				}
			}, error => {								
				Toast({
				  message: error.errorMsg,				  
				  duration: 1000
				});
			});
		},
		goBack() {
			this.$router.go(-1)
		},
		goRecord() {
			this.$router.push("/scoreRule");
		},		
		getScoreInfo() {
			api.scoreGet().then(res => {
				if ( res ) {
					this.scoreInfo = res;
				}
			});
		},
		// 
		getScoreList() {
			let data = this.requestParams;
			api.scoreExchangeCouponRecordList(data.page, data.per_page).then(res => {
				this.loading = false;
				if ( res ) {
					this.recordList = this.recordList.concat(res.exchange_record);
					this.isMore = res.paged.more == 1 ? true : false;
				}
			},error => {
				this.loading = false;
				this.isMore = false;
			});
		},
		// 
		getScoreChageList() {
			let data = this.requestParams;
			api.scoreExchangeCouponList(data.page, data.per_page).then(res => {
				this.loading = false;
				if ( res ) {
					this.exchangeList = this.exchangeList.concat(res.exchange);
					this.isMore = res.paged.more == 1 ? true : false;
				}
			},error => {
				this.loading = false;
				this.isMore = false;
			});
		},
		// 
		touchExplain() {
			MessageBox({
				title: "",				
				message: "</br>",
				confirmButtonText: "",
				confirmButtonClass: "confirm-button-red"
			});
		},
		setCurrentItem(index) {
			if ( index == this.currentIndex ) {
				return;
			}
			this.requestParams = {'page': 0, 'per_page': 10};
			this.currentIndex = index;
			this.recordList = [];
			this.exchangeList = [];
			this.isMore = true;
			this.getMore();
		},
		/*
		 * getMore: 
		 */
		getMore() {			
			this.loading = true;
			this.requestParams.page = ++this.requestParams.page;
			
			if ( this.isMore ) {
				if ( this.currentIndex == 0 ) {
					this.getScoreChageList();
				} else {
					this.getScoreList();
				}
			}
		}
	},
	components: {
		ScoreItem,
		ScoreRecord		
	}
}
</script>

<style lang="scss" scoped>
	.container {
		background-color: $mainbgColor;
		height: 100%;
		.header-info {
			width: 100%;
			height: 170px;
			background: url('../../assets/image/change-icon/e2_bg@2x.png') no-repeat;
			background-size: cover;
			color: #fff;
			font-size: 18px;
			text-align: center;
			.title {
				font-size: 36px;
				padding-top: 28px;
			}
			.desc {
				margin-top: 12px;
				img {
					width: 16px;
					height: 16px;
					display: inline-block;
					vertical-align: middle;		
					margin-top: -3px;			
				}
			}			
		}
		.tab {
			height: 50px;
			line-height: 50px;
			background:rgba(255,255,255,1);
			border-top: 1px solid #E8EAED;
			ul {
				padding: 0px;
				margin: 0px;
				list-style: none;
				display: flex;
				justify-content: space-around;
				align-content: center;
				align-items: center;
				li {
					height: -webkit-fill-available;
					padding: 0px 10px;
					font-size:14px;
					color:rgba(124,127,136,1);
					border-bottom: 2px solid;
					border-bottom-color: transparent;
					&.active {
						color:rgba(242,48,48,1);
						border-bottom-color: #F23030;
					}
				}
			}
		}
		.list {
			margin-top: 10px;
		}
		.empty {
			padding-top: 70px;
			text-align: center;
			img {
				width: 181px;
				height: 164px;
			}
			.desc {
				margin-top: 30px;
				font-size: 18px;
				color: #A0A0A0;
			}
		}
	}
	.header {
		@include header;
		.right {
			font-size: 14px;
			color: #404245;
			img {
				width: 16px;
				height: 16px;
				vertical-align: middle;
				margin-right: 2px;
				margin-top: -4px;
			}
		}
	}
</style>




