// initial state
const state ={
	total_amount: 0, //
	total_price: 0.00, // 
	height: 0 // TODO
}

// mutations
const mutations = {
	//
	calculationAmount(state, amount) {
		state.total_amount = amount;
	},
	calculationPrice(state, price) {
		state.total_price = price;
	},

	setHeight(state, value) {
		state.height = value;
	}
}

export default {
	state,
	mutations,
}



